import React from 'react';
import { BrowserRouter as Router, Route, NavLink } from 'react-router-dom';
import './style.css';

function App() {
  return (
    <Router>
      <div>
        <Header />
        <Route exact path="/" component={Home} />
        <Route path="/tabs" component={Tabs} />
      </div>
    </Router>
  );
}

function Home() {
  return <div className="tab"><h4>Home</h4></div>;
}

function Tab({ match }) {
  return <div className="tab">
            <h5>Requested Param: `{match.params.id}`</h5>
          </div>;
}
     
function Tabs({ match }) {
  return (
    <div className="top-menu">
    <ul>
			<li><NavLink to={`${match.url}/tab-1`} activeClassName="active">Tab-1</NavLink></li>
			<li><NavLink to={`${match.url}/tab-2`} activeClassName="active">Tab-2</NavLink></li>
			<li><NavLink to={`${match.url}/tab-3`} activeClassName="active">Tab-3</NavLink></li>
		</ul>
    <Route path={`${match.path}/:id`} component={Tab} />
    <Route
      exact
      path={match.path}
      render={() => <div className="tab"><h4>Please select a tab</h4></div>}
    />
    </div>
  );
}

function Header() {
  return (
    <div className="top-menu">
		<ul>
			<li><NavLink to="/" activeClassName="active">Home page</NavLink></li>
			<li><NavLink to="/tabs" activeClassName="active">Tabs page</NavLink></li>
		</ul>
	</div>
        
  );
}

export default App;